import React from 'react';

// This component will render custom icons for each symbol
const SymbolIcon = ({ symbol, size = 40 }) => {
  // Styles for the base icon
  const styles = {
    container: {
      width: `${size}px`,
      height: `${size}px`,
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      borderRadius: '8px',
      backgroundColor: symbol.color,
      color: 'white',
      fontWeight: 'bold',
      fontSize: `${size * 0.5}px`,
      boxShadow: '0 2px 5px rgba(0, 0, 0, 0.2)',
      position: 'relative',
      overflow: 'hidden'
    },
    shine: {
      position: 'absolute',
      top: 0,
      left: 0,
      width: '100%',
      height: '30%',
      background: 'linear-gradient(to bottom, rgba(255, 255, 255, 0.3), rgba(255, 255, 255, 0))',
      borderTopLeftRadius: '8px',
      borderTopRightRadius: '8px'
    }
  };

  // Custom rendering based on symbol type
  const renderSymbolContent = () => {
    switch (symbol.id) {
      case '10':
        return <span>10</span>;
      case 'J':
        return <span>J</span>;
      case 'Q':
        return <span>Q</span>;
      case 'K':
        return <span>K</span>;
      case 'A':
        return <span>A</span>;
      case 'Solana':
        return (
          <svg width={size * 0.6} height={size * 0.6} viewBox="0 0 24 24" fill="none">
            <path 
              d="M16 8L8 16M8 8H16V16" 
              stroke="white" 
              strokeWidth="2.5" 
              strokeLinecap="round" 
              strokeLinejoin="round"
            />
          </svg>
        );
      case 'Pepe':
        return (
          <div style={{ 
            fontSize: `${size * 0.4}px`, 
            fontWeight: 'bold',
            color: 'white'
          }}>
            PEPE
          </div>
        );
      case 'Trump':
        return (
          <div style={{ 
            fontSize: `${size * 0.35}px`, 
            fontWeight: 'bold',
            color: 'white'
          }}>
            TRUMP
          </div>
        );
      case 'Bonk':
        return (
          <div style={{ 
            fontSize: `${size * 0.4}px`, 
            fontWeight: 'bold',
            color: 'white'
          }}>
            BONK
          </div>
        );
      case 'PopCat':
        return (
          <div style={{ 
            fontSize: `${size * 0.3}px`, 
            fontWeight: 'bold',
            color: 'white'
          }}>
            POPCAT
          </div>
        );
      case 'Dogwifhat':
        return (
          <div style={{ 
            fontSize: `${size * 0.25}px`, 
            fontWeight: 'bold',
            color: 'white'
          }}>
            DOGWIF
          </div>
        );
      default:
        return <span>?</span>;
    }
  };

  return (
    <div style={styles.container}>
      {renderSymbolContent()}
      <div style={styles.shine}></div>
    </div>
  );
};

export default SymbolIcon;