import React, { useState } from 'react';

const PayoutTable = ({ symbols, payouts }) => {
  const [isExpanded, setIsExpanded] = useState(false);

  const styles = {
    container: {
      marginTop: '30px',
      backgroundColor: 'var(--surface)',
      borderRadius: '15px',
      padding: '20px',
      width: '100%',
      boxShadow: '0 5px 15px rgba(0, 0, 0, 0.1)',
      transition: 'all 0.3s ease'
    },
    header: {
      display: 'flex',
      justifyContent: 'space-between',
      alignItems: 'center',
      cursor: 'pointer',
      padding: '10px'
    },
    title: {
      margin: 0,
      fontSize: '20px',
      fontWeight: 'bold',
      color: 'var(--text)'
    },
    toggleButton: {
      backgroundColor: 'transparent',
      border: 'none',
      color: 'var(--text-secondary)',
      fontSize: '24px',
      cursor: 'pointer',
      transition: 'transform 0.3s ease',
      transform: isExpanded ? 'rotate(180deg)' : 'rotate(0deg)'
    },
    content: {
      overflow: 'hidden',
      maxHeight: isExpanded ? '400px' : '0',
      transition: 'max-height 0.3s ease',
      marginTop: isExpanded ? '15px' : '0'
    },
    payoutSection: {
      marginBottom: '20px'
    },
    payoutTitle: {
      fontSize: '16px',
      fontWeight: 'bold',
      color: 'var(--accent)',
      marginBottom: '10px'
    },
    symbolsGrid: {
      display: 'grid',
      gridTemplateColumns: 'repeat(auto-fill, minmax(80px, 1fr))',
      gap: '10px',
      marginBottom: '15px'
    },
    symbolBox: {
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      justifyContent: 'center',
      padding: '10px',
      borderRadius: '8px',
      backgroundColor: 'var(--surface-light)',
      transition: 'transform 0.2s ease'
    },
    symbolBoxHover: {
      transform: 'translateY(-3px)'
    },
    symbolIcon: {
      width: '40px',
      height: '40px',
      borderRadius: '5px',
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      marginBottom: '5px',
      fontWeight: 'bold',
      color: 'white',
      textShadow: '0 1px 3px rgba(0, 0, 0, 0.3)'
    },
    symbolName: {
      fontSize: '12px',
      color: 'var(--text)',
      textAlign: 'center'
    },
    winRules: {
      display: 'flex',
      justifyContent: 'space-around',
      padding: '15px',
      backgroundColor: 'var(--surface-light)',
      borderRadius: '10px',
      marginTop: '10px'
    },
    winRule: {
      textAlign: 'center'
    },
    winRuleTitle: {
      fontSize: '14px',
      color: 'var(--text-secondary)',
      marginBottom: '5px'
    },
    winRuleValue: {
      fontSize: '18px',
      fontWeight: 'bold',
      color: 'var(--secondary)'
    }
  };

  return (
    <div style={styles.container}>
      <div style={styles.header} onClick={() => setIsExpanded(!isExpanded)}>
        <h3 style={styles.title}>Payout Table</h3>
        <button style={styles.toggleButton}>▼</button>
      </div>
      
      <div style={styles.content}>
        <div style={styles.payoutSection}>
          <div style={styles.payoutTitle}>Available Symbols</div>
          <div style={styles.symbolsGrid}>
            {symbols.map(symbol => (
              <div 
                key={symbol.id}
                style={styles.symbolBox}
                onMouseEnter={e => {
                  e.currentTarget.style.transform = 'translateY(-3px)';
                }}
                onMouseLeave={e => {
                  e.currentTarget.style.transform = 'translateY(0)';
                }}
              >
                <div 
                  style={{
                    ...styles.symbolIcon,
                    backgroundColor: symbol.color
                  }}
                >
                  {symbol.name.length <= 2 ? symbol.name : symbol.name.slice(0, 1)}
                </div>
                <div style={styles.symbolName}>{symbol.name}</div>
              </div>
            ))}
          </div>
        </div>
        
        <div style={styles.payoutSection}>
          <div style={styles.payoutTitle}>Win Combinations</div>
          <div style={styles.winRules}>
            <div style={styles.winRule}>
              <div style={styles.winRuleTitle}>Match 3 symbols</div>
              <div style={styles.winRuleValue}>{payouts[3]} SOL</div>
            </div>
            <div style={styles.winRule}>
              <div style={styles.winRuleTitle}>Match 4 symbols</div>
              <div style={styles.winRuleValue}>{payouts[4]} SOL</div>
            </div>
            <div style={styles.winRule}>
              <div style={styles.winRuleTitle}>Match 5 symbols</div>
              <div style={styles.winRuleValue}>{payouts[5]} SOL</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default PayoutTable;