import React, { useState, useEffect } from 'react';
import SymbolIcon from './SymbolIcon';
import '../animations.css';

const Reel = ({ symbol, spinning, delay }) => {
  const [animating, setAnimating] = useState(false);
  const [animationSymbols, setAnimationSymbols] = useState([]);
  
  useEffect(() => {
    if (spinning) {
      // Start spinning animation after the specified delay
      const timer = setTimeout(() => {
        setAnimating(true);
        generateAnimationSymbols();
      }, delay);
      
      return () => clearTimeout(timer);
    } else {
      setAnimating(false);
    }
  }, [spinning, delay]);
  
  // Generate random symbols for the spinning animation
  const generateAnimationSymbols = () => {
    // This would be replaced with your actual symbols in a real implementation
    const colors = [
      '#FF6B6B', '#4ECDC4', '#FFD166', '#F86624', '#7209B7', 
      '#9945FF', '#54D62C', '#FFC107', '#FF9800', '#F72585', '#3A86FF'
    ];
    
    const animSymbols = [];
    for (let i = 0; i < 20; i++) {
      animSymbols.push({
        id: `anim-${i}`,
        color: colors[Math.floor(Math.random() * colors.length)]
      });
    }
    
    setAnimationSymbols(animSymbols);
  };

  const styles = {
    reel: {
      width: '18%',
      height: '160px',
      backgroundColor: 'var(--background)',
      borderRadius: '10px',
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      position: 'relative',
      overflow: 'hidden',
      boxShadow: 'inset 0 0 10px rgba(0, 0, 0, 0.5)'
    },
    symbolDisplay: {
      width: '80%',
      height: '80%',
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      borderRadius: '8px',
      backgroundColor: symbol ? symbol.color : 'transparent',
      color: '#FFFFFF',
      fontSize: '24px',
      fontWeight: 'bold',
      textShadow: '0 2px 4px rgba(0, 0, 0, 0.3)',
      transition: 'all 0.2s ease',
      animation: animating ? 'none' : 'pop 0.3s ease'
    },
    animationContainer: {
      position: 'absolute',
      top: 0,
      left: 0,
      width: '100%',
      height: '100%',
      display: animating ? 'block' : 'none'
    },
    animationSymbol: {
      position: 'absolute',
      width: '80%',
      height: '80%',
      left: '10%',
      borderRadius: '8px',
      animation: 'spin 0.1s linear infinite'
    },
    reelShine: {
      position: 'absolute',
      top: 0,
      left: 0,
      width: '100%',
      height: '30%',
      background: 'linear-gradient(to bottom, rgba(255, 255, 255, 0.1), transparent)',
      pointerEvents: 'none'
    }
  };

  // Add keyframe animations using CSS-in-JS
  if (!document.getElementById('reel-animations')) {
    const styleSheet = document.createElement('style');
    styleSheet.id = 'reel-animations';
    styleSheet.innerHTML = `
      @keyframes spin {
        0% { transform: translateY(-500%); }
        100% { transform: translateY(500%); }
      }
      @keyframes pop {
        0% { transform: scale(0.8); opacity: 0.5; }
        50% { transform: scale(1.1); }
        100% { transform: scale(1); opacity: 1; }
      }
    `;
    document.head.appendChild(styleSheet);
  }

  return (
    <div style={styles.reel}>
      {!animating && symbol && (
        <div style={styles.symbolDisplay} className={spinning ? '' : 'pop'}>
          <SymbolIcon symbol={symbol} size={60} />
        </div>
      )}
      
      {animating && (
        <div style={styles.animationContainer}>
          {animationSymbols.map((animSymbol, index) => (
            <div
              key={animSymbol.id}
              style={{
                ...styles.animationSymbol,
                backgroundColor: animSymbol.color,
                animationDuration: `${0.05 + (index * 0.01)}s`,
                animationDelay: `${index * 0.02}s`
              }}
            />
          ))}
        </div>
      )}
      
      <div style={styles.reelShine} />
    </div>
  );
};

export default Reel;