import React, { useState, useEffect } from 'react';
import SlotMachine from './components/SlotMachine';
import BackgroundEffects from './components/BackgroundEffects';
import './animations.css';

const App = () => {
  const [wallet, setWallet] = useState(null);
  const [balance, setBalance] = useState(0);
  const [isConnecting, setIsConnecting] = useState(false);

  // Function to simulate connecting wallet
  const connectWallet = () => {
    setIsConnecting(true);
    
    // Simulate connection delay
    setTimeout(() => {
      // For local testing, we'll create a mock wallet
      const mockWallet = {
        publicKey: 'SomeRandomSolanaPublicKey123456789',
        signTransaction: () => Promise.resolve(true)
      };
      
      setWallet(mockWallet);
      setBalance(5.0); // Mock SOL balance
      setIsConnecting(false);
    }, 1500);
  };

  // Styling
  const styles = {
    container: {
      maxWidth: '1200px',
      margin: '0 auto',
      padding: '20px',
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      minHeight: '100vh'
    },
    header: {
      width: '100%',
      display: 'flex',
      justifyContent: 'space-between',
      alignItems: 'center',
      marginBottom: '30px',
      padding: '15px 25px',
      backgroundColor: 'var(--surface)',
      borderRadius: '15px',
      boxShadow: '0 8px 16px rgba(0, 0, 0, 0.2)'
    },
    title: {
      fontSize: '32px',
      fontWeight: 'bold',
      background: 'linear-gradient(90deg, var(--primary), var(--secondary))',
      WebkitBackgroundClip: 'text',
      WebkitTextFillColor: 'transparent',
      margin: 0
    },
    walletInfo: {
      display: 'flex',
      alignItems: 'center',
      gap: '15px'
    },
    balance: {
      backgroundColor: 'var(--surface-light)',
      padding: '8px 15px',
      borderRadius: '10px',
      fontWeight: 'bold',
      color: 'var(--secondary)'
    },
    connectButton: {
      backgroundColor: 'var(--primary)',
      color: 'white',
      border: 'none',
      borderRadius: '10px',
      padding: '10px 20px',
      fontSize: '16px',
      fontWeight: 'bold',
      cursor: 'pointer',
      transition: 'all 0.2s ease',
      boxShadow: '0 4px 6px rgba(0, 0, 0, 0.1)'
    },
    connectingButton: {
      backgroundColor: 'var(--surface-light)',
      opacity: 0.8
    },
    main: {
      width: '100%',
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      flex: 1
    },
    footer: {
      marginTop: '40px',
      textAlign: 'center',
      color: 'var(--text-secondary)',
      fontSize: '14px'
    }
  };

  return (
    <div style={styles.container}>
      <BackgroundEffects />
      <header style={styles.header}>
        <h1 style={styles.title}>Solana Slot Game</h1>
        <div style={styles.walletInfo}>
          {wallet ? (
            <>
              <div style={styles.balance}>{balance.toFixed(2)} SOL</div>
              <div>
                {wallet.publicKey.slice(0, 4)}...{wallet.publicKey.slice(-4)}
              </div>
            </>
          ) : (
            <button 
              style={{
                ...styles.connectButton,
                ...(isConnecting ? styles.connectingButton : {})
              }}
              onClick={connectWallet}
              disabled={isConnecting}
            >
              {isConnecting ? 'Connecting...' : 'Connect Wallet'}
            </button>
          )}
        </div>
      </header>

      <main style={styles.main}>
        {wallet ? (
          <SlotMachine wallet={wallet} />
        ) : (
          <div style={{ marginTop: '100px', fontSize: '24px', textAlign: 'center' }}>
            Connect your wallet to start playing and win SOL!
          </div>
        )}
      </main>

      <footer style={styles.footer}>
        <p>All winnings will be sent directly to your Solana wallet. Play responsibly!</p>
      </footer>
    </div>
  );
};

export default App;