import React, { useState, useEffect, useRef } from 'react';
import Reel from './Reel';
import PayoutTable from './PayoutTable';
import SymbolIcon from './SymbolIcon';
import '../animations.css';

// Define all possible symbols
const SYMBOLS = [
  { id: '10', name: '10', color: '#FF6B6B' },
  { id: 'J', name: 'J', color: '#4ECDC4' },
  { id: 'Q', name: 'Q', color: '#FFD166' },
  { id: 'K', name: 'K', color: '#F86624' },
  { id: 'A', name: 'A', color: '#7209B7' },
  { id: 'Solana', name: 'Solana', color: '#9945FF' },
  { id: 'Pepe', name: 'Pepe', color: '#54D62C' },
  { id: 'Trump', name: 'Trump', color: '#FFC107' },
  { id: 'Bonk', name: 'Bonk', color: '#FF9800' },
  { id: 'PopCat', name: 'PopCat', color: '#F72585' },
  { id: 'Dogwifhat', name: 'Dogwifhat', color: '#3A86FF' }
];

// Define payout values
const PAYOUTS = {
  3: 0.001, // 3 matches = 0.001 SOL
  4: 0.002, // 4 matches = 0.002 SOL
  5: 0.01   // 5 matches = 0.01 SOL
};

const SlotMachine = ({ wallet }) => {
  const [spinning, setSpinning] = useState(false);
  const [reelResults, setReelResults] = useState(Array(5).fill(null));
  const [winAmount, setWinAmount] = useState(0);
  const [message, setMessage] = useState('');
  const [showWinAnimation, setShowWinAnimation] = useState(false);
  const [lastWin, setLastWin] = useState(null);
  const [spinsCount, setSpinsCount] = useState(0);
  
  const spinTimeoutRef = useRef(null);
  const spinSound = useRef(null);
  const winSound = useRef(null);

  useEffect(() => {
    // Initialize with random symbols
    const initialSymbols = Array(5).fill(null).map(() => 
      SYMBOLS[Math.floor(Math.random() * SYMBOLS.length)]
    );
    setReelResults(initialSymbols);

    // Load sounds
    // Note: In a real implementation, you'd want to use proper audio files
    // spinSound.current = new Audio('/path-to-spin-sound.mp3');
    // winSound.current = new Audio('/path-to-win-sound.mp3');

    return () => {
      if (spinTimeoutRef.current) {
        clearTimeout(spinTimeoutRef.current);
      }
    };
  }, []);

  const spin = () => {
    if (spinning) return;
    
    setSpinning(true);
    setWinAmount(0);
    setMessage('Spinning...');
    setShowWinAnimation(false);
    setSpinsCount(prev => prev + 1);
    
    // Play spin sound
    // if (spinSound.current) spinSound.current.play();

    // Simulate spinning delay
    spinTimeoutRef.current = setTimeout(() => {
      // Generate random results
      const results = Array(5).fill(null).map(() => 
        SYMBOLS[Math.floor(Math.random() * SYMBOLS.length)]
      );
      
      setReelResults(results);
      checkWin(results);
      setSpinning(false);
    }, 2000);
  };

  const checkWin = (results) => {
    // Count occurrences of each symbol
    const counts = {};
    results.forEach(symbol => {
      counts[symbol.id] = (counts[symbol.id] || 0) + 1;
    });
    
    // Find the highest match count
    let highestMatchCount = 0;
    let matchedSymbol = null;
    
    Object.entries(counts).forEach(([symbolId, count]) => {
      if (count >= 3 && count > highestMatchCount) {
        highestMatchCount = count;
        matchedSymbol = symbolId;
      }
    });
    
    if (highestMatchCount >= 3) {
      const winningAmount = PAYOUTS[highestMatchCount];
      setWinAmount(winningAmount);
      setLastWin({
        symbol: SYMBOLS.find(s => s.id === matchedSymbol),
        count: highestMatchCount,
        amount: winningAmount
      });
      setMessage(`You won ${winningAmount} SOL!`);
      setShowWinAnimation(true);
      
      // Play win sound
      // if (winSound.current) winSound.current.play();
      
      // In a real implementation, this is where you'd send SOL to the user's wallet
      console.log(`Sending ${winningAmount} SOL to wallet ${wallet.publicKey}`);
      // sendSol(wallet.publicKey, winningAmount);
    } else {
      setMessage('Try again!');
    }
  };

  // UI Styles
  const styles = {
    container: {
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      width: '100%',
      maxWidth: '800px'
    },
    slotMachine: {
      backgroundColor: 'var(--surface)',
      borderRadius: '20px',
      padding: '30px',
      boxShadow: '0 10px 25px rgba(0, 0, 0, 0.3), inset 0 5px 10px rgba(0, 0, 0, 0.2)',
      width: '100%',
      position: 'relative',
      overflow: 'hidden'
    },
    reelsContainer: {
      display: 'flex',
      justifyContent: 'space-between',
      marginBottom: '30px',
      minHeight: '160px',
      position: 'relative'
    },
    spinButton: {
      backgroundColor: spinning ? 'var(--surface-light)' : 'var(--accent)',
      color: 'var(--background)',
      border: 'none',
      borderRadius: '50px',
      padding: '15px 40px',
      fontSize: '20px',
      fontWeight: 'bold',
      cursor: spinning ? 'not-allowed' : 'pointer',
      transition: 'all 0.3s ease',
      boxShadow: '0 5px 15px rgba(0, 0, 0, 0.2)',
      margin: '20px 0',
      opacity: spinning ? 0.7 : 1
    },
    spinButtonHover: {
      transform: 'translateY(-2px)',
      boxShadow: '0 8px 20px rgba(0, 0, 0, 0.3)'
    },
    message: {
      textAlign: 'center',
      fontSize: '24px',
      fontWeight: 'bold',
      padding: '15px',
      marginTop: '10px',
      borderRadius: '10px',
      backgroundColor: showWinAnimation ? 'rgba(20, 241, 149, 0.1)' : 'transparent',
      color: showWinAnimation ? 'var(--secondary)' : 'var(--text)',
      transition: 'all 0.3s ease'
    },
    stats: {
      display: 'flex',
      justifyContent: 'space-between',
      width: '100%',
      marginTop: '20px',
      fontSize: '16px',
      color: 'var(--text-secondary)'
    },
    lastWin: {
      display: 'flex',
      alignItems: 'center',
      gap: '10px'
    },
    symbolIndicator: {
      display: 'inline-block',
      width: '20px',
      height: '20px',
      borderRadius: '50%',
      marginRight: '5px'
    },
    winOverlay: {
      position: 'absolute',
      top: 0,
      left: 0,
      width: '100%',
      height: '100%',
      background: 'radial-gradient(circle, rgba(20, 241, 149, 0.2) 0%, transparent 70%)',
      pointerEvents: 'none',
      opacity: showWinAnimation ? 1 : 0,
      transition: 'opacity 0.5s ease'
    }
  };

  return (
    <div style={styles.container}>
      <div style={styles.slotMachine}>
        <div style={styles.reelsContainer}>
          {reelResults.map((symbol, index) => (
            <Reel 
              key={index} 
              symbol={symbol} 
              spinning={spinning} 
              delay={index * 200} 
            />
          ))}
          <div style={styles.winOverlay} />
      
      {/* Add confetti effect when winning */}
      {showWinAnimation && Array.from({ length: 30 }).map((_, i) => (
        <div
          key={i}
          className="confetti"
          style={{
            left: `${Math.random() * 100}%`,
            top: `${Math.random() * 100}%`,
            backgroundColor: `hsl(${Math.random() * 360}, 80%, 60%)`,
            animationDelay: `${Math.random() * 0.5}s`
          }}
        />
      ))}
        </div>
        
        <button 
          style={{
            ...styles.spinButton,
            ...(spinning ? {} : styles.spinButtonHover)
          }}
          onClick={spin}
          disabled={spinning}
        >
          {spinning ? 'Spinning...' : 'SPIN'}
        </button>
        
        <div style={styles.message}>
          {message}
        </div>
      </div>
      
      <div style={styles.stats}>
        <div>Total spins: {spinsCount}</div>
        {lastWin && (
          <div style={styles.lastWin}>
            Last win: 
            <span 
              style={{
                ...styles.symbolIndicator,
                backgroundColor: lastWin.symbol.color
              }}
            ></span>
            {lastWin.count}x {lastWin.symbol.name} ({lastWin.amount} SOL)
          </div>
        )}
      </div>
      
      <PayoutTable symbols={SYMBOLS} payouts={PAYOUTS} />
    </div>
  );
};

export default SlotMachine;